.toggleDrawer {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    padding: 30px 0;
    box-sizing: border-box;
    cursor: pointer;
}

.toggleDrawer div {
    width: 90%;
    height: 4px;
    background-color: #fff;
}

